<template>
  <v-app>
    <right-sidebar :menuItems="networksData" serverRoute="modbus"></right-sidebar>
    <v-main>
      <div class="table-styling">
        <v-tabs v-model="tab" right :color="`${$vuetify.theme.dark ? '' : 'nubeGreen'}`">
          <breadcrumbs-direction :items="dirs" />
          <v-tab :key="0" style="margin-left: auto"> Data </v-tab>
          <v-tab :key="1"> Debug Messages </v-tab>
          <v-tab :key="2"> System Status </v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item :key="0">
            <v-data-table
              v-model="selected"
              :headers="deviceHeaders"
              :items="network.devices"
              class="elevation-1"
              item-key="uuid"
              show-select
              :item-class="backgroundColor"
              :footer-props="tableProps.FOOTER_PROPS"
              :itemsPerPage="tableProps.ITEMS_PER_PAGE"
              :search="search"
              :custom-filter="filterCriteria"
              :loading="loadingData"
            >
              <template v-slot:top>
                <v-toolbar flat>
                  <v-toolbar-title>Modbus Devices</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <div class="text-left pa-2">
                    <v-btn x-small class="mr-1 btn-height-22 white--text mt-1" color="primary" @click="onClickRefresh"
                      >Refresh<v-icon x-small right>fa-refresh</v-icon></v-btn
                    >
                    <v-btn x-small class="mr-1 btn-height-22 white--text mt-1" color="nubeGreen" @click="dialog = true"
                      >Create<v-icon x-small right>fa-plus</v-icon></v-btn
                    >
                    <v-btn
                      x-small
                      class="mr-1 btn-height-22 white--text mt-1"
                      color="error"
                      @click="clickDeleteMulti"
                      :disabled="selected.length === 0"
                      >Delete<v-icon x-small right>fa-trash-o</v-icon></v-btn
                    >
                    <v-menu offset-y>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          x-small
                          class="mr-1 btn-height-22 white--text mt-1"
                          color="downloadColor"
                          v-bind="attrs"
                          v-on="on"
                          >Download<v-icon x-small right>fa-download</v-icon></v-btn
                        >
                      </template>
                      <v-list>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(network.devices, 'json')">Json</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(network.devices, 'excel')">Excel</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(network.devices, 'csv')">CSV</v-list-item-title>
                        </v-list-item>
                      </v-list>
                    </v-menu>
                    <v-menu offset-y>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          x-small
                          class="mr-1 btn-height-22 white--text mt-1"
                          color="primary"
                          v-bind="attrs"
                          v-on="on"
                          >Import<v-icon x-small right>fa-vcard-o</v-icon></v-btn
                        >
                      </template>
                      <v-list>
                        <v-list-item link>
                          <v-list-item-title @click="clickImportJson">Json</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="dialogImportExcel = true">Excel</v-list-item-title>
                        </v-list-item>
                      </v-list>
                    </v-menu>
                    <v-btn
                      x-small
                      class="mr-1 btn-height-22 white--text mt-1"
                      color="exportColor"
                      @click="clickJsonExport(selected)"
                      :disabled="selected.length === 0"
                      >Export<v-icon x-small right>fa-upload</v-icon></v-btn
                    >
                  </div>
                  <v-spacer></v-spacer>
                  <v-text-field
                    v-model="search"
                    @input="onSearchChange"
                    append-icon="fa-search"
                    label="Search"
                    hide-details
                  ></v-text-field>
                </v-toolbar>
              </template>

              <template v-slot:[`item.actions`]="{ item }">
                <v-icon small @click="editItem(item)" color="primary">fa-pencil</v-icon>&nbsp;
                <v-icon small @click="clickDelete(item)" color="error">fa-trash-o</v-icon>&nbsp;
                <v-icon small @click="onClickOpen(item)">fa-caret-square-o-up</v-icon>
              </template>
              <template v-slot:[`item.name`]="{ item }">
                <a @click="onClickOpen(item)"> {{ item.name }} </a>
              </template>
              <template v-slot:[`item.enable`]="{ item }">
                <v-checkbox v-model="item.enable" @click="updateDevice(item)"></v-checkbox>
              </template>
              <template v-slot:[`item.supports_multiple_rw`]="{ item }">
                <v-checkbox v-model="item.supports_multiple_rw" @click="updateDevice(item)"></v-checkbox>
              </template>
              <template v-slot:[`item.address`]="{ item }">
                <v-edit-dialog :return-value.sync="item.address" @save="updateDevice(item)" large>
                  <b>{{ item.address }}</b>
                  <template v-slot:input>
                    <v-text-field
                      v-model="item.address"
                      type="number"
                      min="0"
                      :rules="[() => !isExistingvalue(item, 'address') || 'Address Existed']"
                      @input="val => (item.address = val ? Number(val) : 0)"
                    ></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:[`item.ping_point`]="{ item }">
                <v-edit-dialog :return-value.sync="item.ping_point" @save="updateDevice(item)" large>
                  <b>{{ item.ping_point }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.ping_point" placeholder="(Func:Addr:Len 1:1:1)"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>

              <template v-slot:[`header.enable`]>
                {{ toUpperTextKey('enable') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('enable')" color="primary">fa-pencil</v-icon>&nbsp;
              </template>
              <template v-slot:[`header.supports_multiple_rw`]>
                {{ toUpperTextKey('supports_multiple_rw') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('supports_multiple_rw')" color="primary">fa-pencil</v-icon
                >&nbsp;
              </template>
              <template v-slot:[`header.ping_point`]>
                {{ toUpperTextKey('ping_point') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('ping_point')" color="primary">fa-pencil</v-icon>&nbsp;
              </template>

              <template v-slot:no-data> No data </template>
            </v-data-table>
          </v-tab-item>
          <v-tab-item :key="1">
            <mqtt-console :editable="false" :subscriptions="debugSubscriptions"></mqtt-console>
          </v-tab-item>
          <v-tab-item :key="2">
            <system-status-table server="modbus"></system-status-table>
          </v-tab-item>
        </v-tabs-items>
      </div>
    </v-main>

    <!-- Create/Edit dialog-->
    <v-dialog v-model="dialog" max-width="700px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field v-model="network.name" disabled class="centered-input" />
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field
                    @input="changeName"
                    v-model="editedItem.name"
                    label="device name"
                    required
                    :rules="[() => !isExistingvalue(editedItem, 'name') || 'Name Existed', rules.required]"
                  />
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-checkbox v-model="editedItem.enable" label="enable" />
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-checkbox v-model="editedItem.supports_multiple_rw" label="supports multiple r/w" />
                </v-col>
                <!-- <v-col cols="12" sm="6" md="4" v-if="network.type == 'TCP'">
                  <v-text-field v-model="editedItem.tcp_ip" label="IP" :rules="[() => !isExistingvalue(editedItem, 'tcp_ip') || 'IP Existed']"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4" v-if="network.type == 'TCP'">
                  <v-text-field v-model="editedItem.tcp_port" label="Port" type="number"></v-text-field>
                </v-col> -->
                <v-col cols="12" sm="6" md="3">
                  <v-checkbox v-model="editedItem.zero_based" label="zero mode" />
                </v-col>
                <v-col cols="12" sm="6" md="8">
                  <v-text-field v-model="editedItem.ping_point" label="ping point (Func:Addr:Len 1:1:1)" />
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field
                    v-model="editedItem.address"
                    label="address"
                    type="number"
                    :rules="[() => !isExistingvalue(editedItem, 'address') || 'Address Existed']"
                    @input="val => (editedItem.address = val ? Number(val) : 0)"
                  />
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="save" :disabled="!valid">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import wizard dialog-->
    <v-dialog v-model="dialogImport">
      <v-toolbar flat>
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="closeDialogImport">
          <v-icon>fa-times-circle</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1"> Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2"> Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport"></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn
              x-small
              class="mr-1 btn-height-22 white--text"
              color="primary"
              @click="clickContinue()"
              :disabled="!dataImport"
            >
              Continue
            </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="validWizard" ref="form_wizard" lazy-validation>
              <v-data-table
                :headers="deviceHeaders"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="uuid"
                :footer-props="tableProps.FOOTER_PROPS"
                :itemsPerPage="tableProps.ITEMS_PER_PAGE"
                :search="searchWizard"
                :loading="savingImport"
              >
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field
                      v-model="searchWizard"
                      append-icon="fa-search"
                      label="Search"
                      hide-details
                    ></v-text-field>
                  </v-toolbar>
                </template>

                <template v-slot:[`item.actions`]="{ item }">
                  <v-icon small color="error" @click="clickDeleteImport(item)"> fa-trash-o </v-icon>
                  <v-icon small color="nubeGreen" v-if="isClickSaveImport && isExistingvalue(item, 'uuid')"
                    >fa-check-circle-o</v-icon
                  >
                  <v-icon
                    small
                    color="info"
                    @click="createDevice(item)"
                    v-if="isClickSaveImport"
                    :disabled="isClickSaveImport && isExistingvalue(item, 'uuid')"
                    >fa-refresh</v-icon
                  >
                </template>
                <template v-slot:[`item.name`]="{ item }">
                  <v-text-field
                    v-if="isClickSaveImport && isExistingvalue(item, 'uuid')"
                    v-model="item.name"
                    style="width: 100px"
                    disabled
                  ></v-text-field>
                  <v-text-field
                    v-else
                    v-model="item.name"
                    style="width: 100px"
                    required
                    :rules="[() => !isExistingvalue(item, 'name') || 'Name Existed', rules.required]"
                  ></v-text-field>
                </template>
                <template v-slot:[`item.address`]="{ item }">
                  <v-text-field
                    v-if="isClickSaveImport && isExistingvalue(item, 'uuid')"
                    v-model="item.address"
                    style="width: 100px"
                    disabled
                  ></v-text-field>
                  <v-text-field
                    v-else
                    v-model="item.address"
                    type="number"
                    style="width: 100px"
                    required
                    :rules="[() => !isExistingvalue(item, 'address') || 'Address Existed', rules.required]"
                  ></v-text-field>
                </template>
              </v-data-table>
            </v-form>
            <v-btn x-small class="mr-1 btn-height-22 white--text" color="secondary" @click="e1 = 1"> Back </v-btn>
            <v-btn
              x-small
              class="mr-1 btn-height-22 white--text"
              color="primary"
              @click="saveImport"
              :disabled="!validWizard || savingImport"
            >
              Save
            </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Import Excel dialog-->
    <import-from-excel-dialog
      :is-open="dialogImportExcel"
      :on-close="() => (dialogImportExcel = false)"
      :on-confirm="convertExcelToJson"
    ></import-from-excel-dialog>

    <!-- Delete dialog -->
    <confirm-dialog
      :is-open="dialogDelete"
      :on-close="closeDelete"
      :on-confirm="confirm"
      dialog-text="Do you want to delete ?"
    ></confirm-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="300px">
      <v-card>
        <v-card-title>Mass Edit</v-card-title>
        <v-card-text>
          <v-checkbox
            v-if="typeof editAll[editField] === 'boolean'"
            v-model="editAll[editField]"
            :label="toUpperTextKey(editField)"
          />
          <v-text-field
            v-if="typeof editAll[editField] === 'number'"
            min="0"
            type="number"
            v-model.number="editAll[editField]"
            :label="toUpperTextKey(editField)"
            @input="val => (editAll[editField] = val ? Number(val) : 0)"
          />
          <v-text-field
            v-if="typeof editAll[editField] === 'string'"
            v-model="editAll[editField]"
            :label="toUpperTextKey(editField)"
          />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import axios from '../../axios';
import * as xlsx from 'xlsx';
import _cloneDeep from 'lodash/cloneDeep';
import _forIn from 'lodash/forIn';
import MqttConsole from '../Mqtt/MqttConsole';
import RightSidebar from '../Common/RightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import ImportFromExcelDialog from '../Common/ImportFromExcelDialog';
import SystemStatusTable from '../Common/SystemStatusTable.vue';
import BreadcrumbsDirection from '../Common/BreadcrumbsDirection.vue';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import * as modbusServerService from '../../services/modbusServerService';
import { RULES, TOPIC, DEVICE_HEADERS, DEVICE_DEFAULT, DEVICE_EDIT_ALL_DEFAULT } from './modbusConstant';
import {
  copyToClipboard,
  isJsonString,
  exportJSON,
  exportCSV,
  exportExcel,
  toUpperTextKey,
  conventionNaming,
} from '../../utils/utils';

export default {
  components: {
    MqttConsole,
    RightSidebar,
    ConfirmDialog,
    ImportFromExcelDialog,
    SystemStatusTable,
    BreadcrumbsDirection,
  },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    topic: TOPIC,
    rules: { ...RULES },
    deviceHeaders: [...DEVICE_HEADERS],
    editedItem: { ...DEVICE_DEFAULT },
    editAll: { ...DEVICE_EDIT_ALL_DEFAULT },
    tableProps: { ...DATA_TABLE_PROPS },
    dialog: false,
    dialogDelete: false,
    dialogImport: false,
    dialogImportExcel: false,
    dialogMassEdit: false,
    tab: 0,
    valid: false,
    editedIndex: -1,
    dataImport: '',
    network: {},
    selected: [],
    action: '',
    importArray: [],
    e1: 1,
    allPoints: [],
    search: '',
    searchItems: [],
    searchWizard: '',
    editField: '',
    intervalId: null,
    devicesIncludeChild: [],
    isClickSaveImport: false,
    dirs: [],
    validWizard: false,
    savingImport: false,
    loadingData: false,
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Create Device' : 'Edit Device';
    },
    networksData() {
      const { mbNetworks } = this.$store.state;
      return mbNetworks;
    },
    debugSubscriptions() {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.modbus;
    },
    setting() {
      return this.$store.state.setting;
    },
  },
  watch: {
    'dialog'(val) {
      val || this.close();
    },
    'dialogDelete'(val) {
      val || this.closeDelete();
    },
    '$route.params.id': function (val, oldVal) {
      if (val != oldVal) {
        this.getData();
      }
    },
  },
  beforeRouteLeave(to, from, next) {
    clearInterval(this.intervalId);
    this.$store.commit('showOverlay');
    axios.post(`mqtt/unsubscribe`, { topic: this.topic }).finally(() => {
      this.$store.commit('hideOverlay');
      next();
    });
  },
  mounted() {
    this.getData();
    //set auto refresh
    if (this.setting.refreshTime && this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(() => {
        this.getData();
        if (this.setting.toastAutoRefresh) {
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    async getData() {
      this.loadingData = true;
      this.network = await modbusServerService.fetchNetwork(this.$route.params.id, { with_children: true });
      this.getDirs();
      this.editedItem.network_uuid = this.network.uuid;

      //update MbNetworksData
      this.updateMbNetworksData(this.network);
      this.loadingData = false;
    },
    getDirs() {
      this.dirs = [{ text: 'NETWORKS', link: '/modbus/networks', disabled: false }];
      this.dirs.push({ text: `NET: ${this.network.name}`, disabled: true });
    },
    updateMbNetworksData(newNetwork) {
      const indexNetWork = this.networksData.findIndex(nw => nw.uuid == newNetwork.uuid);
      if (indexNetWork > -1) {
        Object.assign(this.networksData[indexNetWork], newNetwork);
        this.$store.commit('changeMbNetworksData', this.networksData);
      }
    },
    async getDeviceIncludeChild(item) {
      const device = await modbusServerService.fetchDevice(item.uuid, { with_children: true });
      this.devicesIncludeChild.push(device);
    },
    onClickOpen(item) {
      this.$router.push({ path: `/modbus/devices/${item.uuid}/points` });
    },
    editItem(item) {
      this.editedIndex = this.network.devices.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    async save() {
      if (this.$refs.form.validate()) {
        if (this.editedIndex > -1) {
          this.updateDevice(this.editedItem);
        } else {
          this.createDevice(this.editedItem);
        }
      }
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.editedItem = item;
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.network.devices.indexOf(this.editedItem);
      this.network.devices.splice(this.editedIndex, 1);
      await modbusServerService.removeDevice(this.editedItem.uuid);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.editedItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, { ...DEVICE_DEFAULT, network_uuid: this.network.uuid });
        this.editedIndex = -1;
        if (this.$refs.form) this.$refs.form.resetValidation();
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, { ...DEVICE_DEFAULT, network_uuid: this.network.uuid });
        this.editedIndex = -1;
      });
    },
    async clickImportJson() {
      this.devicesIncludeChild = [];
      this.dialogImport = true;
      const promises = [];
      for (const item of this.selected) {
        promises.push(this.getDeviceIncludeChild(item));
      }
      await Promise.all(promises);
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.devicesIncludeChild) : '';
      this.e1 = 1;
    },
    convertExcelToJson(file) {
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(file);
      fileReader.onload = event => {
        let data = event.target.result;
        let workbook = xlsx.read(data, { type: 'binary' });
        workbook.SheetNames.forEach(sheet => {
          let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
          const items = rowObject.map(i => {
            for (let key in i) {
              const newKey = key.trimRight('');
              const value = i[key];
              delete i[key];
              i = { ...i, [newKey]: value };
            }
            i.network_uuid = this.network.uuid;
            i = this.getPointsArray(i);
            return i;
          });
          this.dataImport = JSON.stringify(items);
          this.dialogImportExcel = false;
          this.dialogImport = true;
          this.e1 = 1;
        });
      };
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    async saveImport() {
      if (this.importArray && this.importArray.length > 0) {
        if (this.$refs.form_wizard.validate()) {
          this.savingImport = true;
          this.isClickSaveImport = true;
          try {
            const promises = [];
            for (const item of this.importArray) {
              if (!item.uuid) {
                item.network_uuid = this.network.uuid; //update to the current network_uuid
                item.name = conventionNaming(item.name, this.setting);
                promises.push(this.createDevice(item));
              }
            }
            await Promise.all(promises);
            this.closeDialogImport();
            this.selected = [];
          } catch (ex) {
          } finally {
            this.savingImport = false;
          }
        }
      }
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.isClickSaveImport = false;
      this.$nextTick(() => {
        this.dataImport = '';
      });
    },
    clickContinue() {
      if (!isJsonString(this.dataImport)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport).map(device => {
        delete device.uuid;
        return device;
      });
      this.e1 = 2;
    },
    isExistingvalue(item, fieldName) {
      if (!this.network.devices) return false;
      let value = item[fieldName];
      let devices = _cloneDeep(this.network.devices);
      const index = devices.findIndex(d => d.uuid == item.uuid);
      if (index !== -1) {
        devices.splice(index, 1); //when edit
      }
      return devices.find(d => d[fieldName] === value) != null;
    },
    async clickJsonExport() {
      this.devicesIncludeChild = [];
      const promises = [];
      for (const item of this.selected) {
        promises.push(this.getDeviceIncludeChild(item));
      }
      await Promise.all(promises);
      const status = copyToClipboard(JSON.stringify(this.devicesIncludeChild));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickDownload(array, type) {
      const data = this.convertToExportData(JSON.parse(JSON.stringify(array)));
      if (type === 'csv') {
        return exportCSV('devices', data);
      }
      if (type === 'excel') {
        return exportExcel('devices', data);
      }
      if (type === 'json') {
        return exportJSON('devices', JSON.stringify(array));
      }
    },
    convertToExportData(array) {
      const data = _cloneDeep(array).map(device => {
        _forIn(device, (value, key) => {
          if (key == 'points') {
            //get points
            device.points.forEach((point, indexP) => {
              _forIn(point, (point_value, point_key) => {
                if (point_key != 'point_store') {
                  const pointKey = `point[${indexP}].${point_key}`;
                  device = { ...device, [pointKey]: point_value };
                } else {
                  //point_store
                  _forIn(point.point_store, (point_store_value, point_store_key) => {
                    const pointStoreKey = `point[${indexP}].point_store.${point_store_key}`;
                    device = { ...device, [pointStoreKey]: point_store_value };
                  });
                  delete point.point_store;
                }
              });
            });
            delete device.points;
          }
        });
        return device;
      });
      return data;
    },
    getPointsArray(device) {
      let points = [];
      let indexs = [];
      let pointKeys = [];
      Object.keys(device).forEach(key => {
        let keys = key.split('.');
        let pointKey = '';
        let pointIndex = null;
        if (keys.length > 1) {
          pointKey = keys[1];
          if (!pointKeys.includes(pointKey)) {
            pointKeys.push(pointKey);
          }
          pointIndex = Number(keys[0].split('[')[1].slice(0, -1));
          if ((pointIndex || pointIndex === 0) && !indexs.includes(pointIndex)) {
            indexs.push(pointIndex);
          }
        }
      });
      indexs.forEach(i => {
        let point = {};
        pointKeys.forEach(pk => {
          point = { ...point, [pk]: device[`point[${i}].${pk}`] };
        });
        points.push(point);
      });
      device.points = points;
      return device;
    },
    updateDevice(data) {
      modbusServerService.updateDevice(data.uuid, data).then(response => {
        const index = this.network.devices.findIndex(d => d.uuid == response.uuid);
        Object.assign(this.network.devices[index], response);
        this.close();
        return this.$toasted.show('update success');
      });
    },
    async createDevice(data) {
      // if(this.network.type == 'RTU'){
      //   delete data.tcp_ip;
      //   delete data.tcp_port;
      // }
      data.type = this.network.type;
      await modbusServerService.createDevice(data).then(response => {
        this.network.devices.push(response);
        if (this.importArray && this.importArray.length > 0) {
          const index = this.importArray.findIndex(n => n.name == response.name);
          if (index > -1) {
            Object.assign(this.importArray[index], response);
          }
        }
        this.close();
        this.$toasted.show('create success');
        if (data.points && data.points.length > 0) {
          for (const point of data.points) {
            point.device_uuid = response.uuid;
            point.register = point.register ? point.register : 0;
            point.register_length = point.register_length ? point.register_length : 0;
            point.enable = point.enable ? point.enable : false;
            this.createPoint(point);
          }
        }
      });
    },
    async createPoint(data) {
      await modbusServerService.createPoint(data);
    },
    backgroundColor(item) {
      let classColor = '';
      if (item.fault) {
        classColor = 'nubeYellow';
      } else {
        classColor = !item.enable ? 'nubeLightGrey' : '';
      }
      return classColor;
    },
    saveMassEdit(value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updateDevice(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName) {
      if (this.selected.length < 1) {
        return this.$toasted.show('Please select item first!', { type: 'error' });
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
      this.editAll = { ...DEVICE_EDIT_ALL_DEFAULT };
    },

    onClickRefresh() {
      this.getData();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    changeName(val) {
      this.editedItem.name = conventionNaming(val, this.setting);
    },
    onSearchChange() {
      this.searchItems = this.search.split(',').map(val => val.trim());
    },
    filterCriteria(value, search, item) {
      return (
        value != null &&
        this.searchItems &&
        Array.isArray(this.searchItems) &&
        this.searchItems.length > 0 &&
        this.searchItems.every(val => typeof val === 'string') &&
        this.searchItems.some(searchVal => value.toString().toLowerCase().indexOf(searchVal.toLowerCase()) !== -1)
      );
    },
  },
};
</script>
